  .posts {
    align-content: center;
    position: center;
    height: auto;
    box-sizing: border-box;
    border: 1px solid #dddddd;
    padding: 2px 0px;
    float: center;
    margin-bottom: 10px;
    flex-wrap: initial;
    animation-timing-function: linear;
  }

  .post {
    position: center;
    height: auto;
    width: 600px;
    box-sizing: border-box;
    border: 1px solid #dddddd;
    padding: 2px 0px;
    float: center;
    margin-bottom: 10px;
  }
  .post:hover :hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.9);
  }

